import HomeMainSection from './components/home-main-section'
import Section2 from './components/section-2'
import Section3 from './components/section-3-advantage.vue'
import Section4 from './components/section-4-ads.vue'
import Section5 from './components/section-5-opportunity.vue'
import Section6 from './components/section-6-apply-faq.vue'
const initialSlider = ()=>{

  if ($('#main-slider').length) {

     setTimeout(() => {
        var sliderWidth = $("#main-slider").data("slider-width");
      var sliderHeigth = $("#main-slider").data("slider-height");
      var sliderArrows = $("#main-slider").data("slider-arrows");
      var sliderButtons = $("#main-slider").data("slider-buttons");

      $( '#main-slider' ).sliderPro({
          width:  sliderWidth,
          height: sliderHeigth,
          arrows: sliderArrows,
          buttons: sliderButtons,
          fade: true,
          fullScreen: true,
          touchSwipe: false,
          autoplay: true
      });
     }, 250);
  }
}
export default {
  components:{HomeMainSection,Section2,Section3,Section4,Section5,Section6},
  mounted(){
   if(window.jQuery){
     initialSlider()
   }

  }
}
